export class API {
    static getBaseBenefits(){
        const BASE_BENEFITS_URL = API_URL+'base-benefit-plans/';
        return fetch(BASE_BENEFITS_URL, {
		   	method: 'get',
		   	headers: {'Content-Type':'application/json'}
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }

    static getFacilities(if_clinic_based: boolean){
        const FACILITIES_URL = API_URL
                                +   'facilities'
                                +   (if_clinic_based ? '?filter={"where":{"type":"Clinic-based"}}' : '');

        return fetch(FACILITIES_URL, {
            method: 'get',
            headers: {'Content-Type':'application/json'}
        }).then(response => response.json()
        ).catch(error => {
            console.log('client error: ', error);
        });
    }

    static getBenefitsTree(tree_id: any){
        const TREE_URL = API_URL+'benefit-plans/tree/'+tree_id;
        return fetch(TREE_URL, {
            method: 'get',
            headers: {'Content-Type':'application/json'}
        }).then(response => response.json()
        ).catch(error => {
            console.log('client error: ', error);
        });
    }

    static getContracts(){
        const CONTRACTS_URL = API_URL+'benefit-plans?filter[where][and][0][level]=2&filter[where][and][1][code]=MCT';
        return fetch(CONTRACTS_URL, {
            method: 'get',
            headers: {'Content-Type':'application/json'}
        }).then(response => response.json()
        ).catch(error => {
            console.log('client error: ', error);
        });
    }

    static patchProposal(clientID: string, proposalID: string, data: any){
        const PROPOSAL_URL = API_URL+'benefit-plans/proposals/' + clientID + '/' + proposalID;
        return fetch(PROPOSAL_URL, {
          method: 'PATCH',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(data),
        })
          .then(response => response.json())
          .catch(error => {
            console.log('client error: ', error);
          });
    }

    static postProposal(payload: any){
        const PROPOSAL_URL = API_URL+'benefit-plans/proposals/';
        return fetch(PROPOSAL_URL, {
            method: 'post',
            headers: {'Content-Type':'application/json'},
            body: JSON.stringify(payload)
        }).then(response => response.json()
        ).catch(error => {
            console.log('client error: ', error);
        });
    }


    static getClient(client_id: string){
        let CLIENT_URL = API_URL+'clients/' + client_id;
        return fetch(CLIENT_URL, {
            method: 'get',
            headers: {'Content-Type':'application/json'},
        }).then(response => response.json()
        ).catch(error => {
            console.log('client error: ', error);
            return error;
        });
    }

    static patchClient(client_id: string, payload: any){
        let CLIENT_URL = API_URL+'clients/' + client_id;
        return fetch(CLIENT_URL, {
            method: 'put',
            headers: {'Content-Type':'application/json'},
            body: JSON.stringify(payload)
        }).then(response => {
            return response;
        }).catch(error => {
            return error;
        });
    }
}